.filter-container {
  display: flex;
  flex-direction: column;
  width: 220px;
  padding: 5%;
  box-shadow: 0 0 3px rgba(177, 181, 196, 0.5);
  border-radius: 16px;
  margin-bottom: 30px;
}

.filter-location {
  display: flex;
  align-items: center;
}

.filter-img {
  padding: 2%;
  width: 10%;
}

.filter-check {
  margin: 15px;
  white-space: nowrap;
}

.appear{
  animation: fade-in 0.5s;
  animation-fill-mode: forwards;
}

.disappear {
  animation: fade-out 0.3s;
  animation-fill-mode: forwards;
}

@keyframes fade-in {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@keyframes fade-out {
  from{
    opacity: 1;
  }
  to{
    opacity: 0;
  }
}

.right {
  margin-left: auto;
}

.filter-check>label, .filter-check>input, .filter-img{
    cursor: pointer;
}

.filter-check > input {
  margin: 0px 10px 0px 0px;
  width: 15px;
  height: 15px;
  vertical-align: middle;

  box-shadow: 0 0 0 1px #d9dadb;
  appearance: none;
  
}

.filter-check > input[type="checkbox"] {
  border-radius: 3px;
}

.filter-check > input[type="checkbox"]:checked {
  box-shadow: 0 0 0 1px #47abd9;
  background-color: #47abd9;
  background: url(../../../assets/images/check.png) no-repeat;
  background-position: center center;
  background-size: 110%;
}

.filter-check > input[type="radio"] {
  border-radius: 50%;
  border: 3px solid white;
  background-color: white;
}

.filter-check > input[type="radio"]:checked {
  background-color: #47abd9;
}

.on {
  color: #47abd9;
}

.bold {
  font-weight: bold;
}

.filter-inner {
  display: block;
  margin-left: 2px;
  margin-right: 2px;
  padding-top: 0.35em;
  padding-bottom: 0.625em;
  padding-left: 0.5em;
  padding-right: 0.75em;
}

